
import VHeader from '@/components/v-header/v-header'
import Scroll from '@/components/scroll/scroll'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


import axios from '@/api/axiosApi'

export default {
data () {
return {
mainHeight: 500,
strategyList: []
}
},
components: {
VHeader, Scroll, Grid, GridItem
},
created () {
this._getStrategyList()
},
mounted () {
this.mainHeight = window.innerHeight - this.$refs.scroll.$el.offsetTop
},
methods: {
_getStrategyList() {
axios.get(this, '/v1/tourismStrategy', null, (data) => {
this.strategyList = data
})
},
clickItem (item) {
this.$router.push(`/address/strategyList/${item.id}`)
}
}
}



